/********* HEADER *********/
.Header{
    background-image: -webkit-gradient(linear, left top, left bottom, from(#3ab0ec), to(#3ab0ec));
    background-image: -webkit-linear-gradient(#3ab0ec, #3ab0ec);
    background-image: -o-linear-gradient(#3ab0ec, #3ab0ec);
    background-image: linear-gradient(#3ab0ec, #3ab0ec);
    padding-top: 47px;
    padding-left: 0px;
    padding-right: 0px;
}
.Header .logo_alcaldia{
    text-align: right;
}

.Header .contenedorLogos{
    background-color: #3ab0ec;
    padding: 15px 0px;
}

.Header .contenedorLogos>div.row{
    max-width: 100%;
}

.img_header{
    max-width: 200px;
}

.thirdLineHeader{
    background: #3ab0ec;
    padding: 20px 5px;
    max-width: 100%;
}

#text_state_session{
    color:black;
}

.thirdLineHeader .icono img{
    max-width: 40px;
}

.thirdLineHeader .infoUser{
    position: absolute;
    top: 0; 
    height: 100%;
    text-align: right;
    font-family: "Gotham Book";
    font-size: calc(90% + 0.01vw);
    line-height: 50px;
    text-transform: lowercase;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: end;
        justify-content: flex-end;
    padding: 0px;
  }

.thirdLineHeader .infoUser *{
    line-height: normal;
}

.thirdLineHeader .infoUser .userIcon{
    max-width: 40px;
    max-height: 40px;
    margin-left: 10px;
}

.thirdLineHeader .infoUser .name, .thirdLineHeader .infoUser .logout{
    font-family: 'Gotham Medium';
    text-transform: capitalize;

}

.thirdLineHeader .infoUser>div{
    width: 100%;
    height: 50%;
    margin: auto;
}

.thirdLineHeader .welcome{
    color: white;
    margin-right: 5px;
}

.thirdLineHeader .welcome:first-letter{
    text-transform: uppercase;
}

.thirdLineHeader .infoUser .logoutFull{
    display: block;
    position: absolute;
    padding: 10px 40px;
    background-color: #3ab0ec;
    bottom: -45px;
    text-transform: capitalize;
    font-family: "Gotham Book";
    font-size: calc(100% + 0.3vw);
    text-align: left;
    z-index: 2;
}

.thirdLineHeader .infoUser .logoutFull img{
    max-width: 25px;
}

.thirdLineHeader .infoUser .logoutFull::after{
    content: "";
    position: absolute;
    bottom: 100%;
    left: 90%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent #3ab0ec transparent;
}

@media (max-width: 575.98px) {
    .img_header{width: 75px;}
    #img_menu{ height: 20px;}
}

@media (max-width: 767.98px) {
    .img_header{width: 100px;}
      #img_menu{ height: 30px;}
}

@media (min-width: 768px){
    .thirdLineHeader .infoUser .logout{
        display: none;
    }
}

@media (max-width: 768px){
    .thirdLineHeader{
        margin: 0;
        padding: 0.5em;
    }

    .thirdLineHeader>div{
        margin: 0;
        padding: 0;
    }

    .thirdLineHeader .infoUser .userIcon, .thirdLineHeader .welcome{
        display: none;
    }

    .thirdLineHeader .infoUser{
        position: relative;
        top:-1.8em;
       z-index: 2;
    
    }

    .thirdLineHeader .menu{
        padding: 0;
    }

    .thirdLineHeader .infoUser .logout{
        position: absolute;
        left: 10px;
        z-index: 2;
    }

    .thirdLineHeader .infoUser .logout img{
        max-width: 25px;
    }

    .thirdLineHeader .infoUser .logoutFull{
        display: none;
    }

}

/*********** MENU **********/

nav{
    padding: 0 !important;
}

.menu{
    display: inline-block;
}

.navbar-light .navbar-nav .nav-link{
    color: white;
    font-family: "Gotham Book";
    font-size: calc(100% + 0.1vw);
}

.navbar-light .navbar-nav .nav-link:hover, .navbar-light .navbar-nav li.nav-item.active .nav-link{
    border: solid 2px #5D5D5D;
    color: #5D5D5D;
    font-family: 'Gotham Medium';
}

.navbar-light .navbar-nav li{
    margin-right: 12px;
}

.navbar-light .navbar-toggler{
    margin-bottom: 10px;
    z-index: 10;
}

.navbar-light .navbar-toggler-icon{
    background-image: url('../images/header/icono_menu_full.png');
    width: 40px;
    height: 40px;
}

.navbar-light .navbar-toggler{
    border: none;
    display: block;
    margin: 0 auto;
}

.desplegado{
    display: none;
}

.menu #drop-subMenu{
    border: none;
    width: 100%;
    text-align: left;
}

.menu .dropdown-menu{
    background-color: #3ab0ec;
}

.menu .dropdown-menu.show{
    border: none;
    padding: 10px;
}

.menu .dropdown-menu .nav-item:hover{
    background-color: transparent;
}

.menu .dropdown-menu .nav-item{
    width: auto;
    font-family: "Gotham Book";
    margin-right: 3em;
}

.menu .dropdown-menu .nav-item a{
    color: #3E3E3F;
}

/*****MENU-ROOT-VIEW*****/
.menu .menuRootView .nav-item {
    text-align: left;
}
.menu .menuRootView .nav-item a {
    color: #3E3E3F;
}

@media (min-width: 768px) and (max-width: 1200px) {
    .navbar-light .navbar-nav .nav-link{
        font-size: 15px;
    }
}

@media (max-width: 768px) {
    .navbar-light .navbar-nav li{
        margin-right: 0;
    }

    .thirdLineHeader{
        padding-bottom: 0;
    }

    .navbar-light .navbar-toggler{
        margin-bottom: 10px;
    }

    .thirdLineHeader .collapse{
        width: 100%;
        background-color:#3ab0ec;
        padding:10px;
    }

    .navbar-light .navbar-nav .nav-link{
        color: #5D5D5D;
        font-family: "Gotham Book";
        font-size: calc(100% + 0.8vw);
        float: left;
        display: inline;
        padding: 10px 10px;
    }

    .navbar-light .navbar-toggler-icon{
        background-image: url('../images/header/icono_menu.png');
    }

    .desplegado{
        display: block;
        margin: 0 auto;
        max-width: 30px;
    }

    .menu .dropdown-menu{
        background-color: #3ab0ec;
    }

    .menu .dropdown-menu .nav-item a{
        color: #5D5D5D;
        background-color: transparent;
    }

}

