#card-container {
    cursor: pointer;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19), 0 -4px 8px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19), 0 -4px 8px 0 rgba(0, 0, 0, 0.2);
}
#card-image {
    opacity: 1;
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    overflow: hidden;
}
#card-container:hover #card-image {
    opacity: 0.3;
}
#card-overlay-image {
    position: absolute;
    -webkit-transform: translateY(6vw);
        -ms-transform: translateY(6vw);
            transform: translateY(6vw);
    opacity: 0;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}
#card-container:hover #card-overlay-image {
    opacity: 1;
}
#candidate-card-title {
    margin-bottom: 0;
