        font-size: calc(12px + 7 * ((100vw - 768px)/431));
    }
    .bannerVotoP .contenedorTextoBanner a {
        font-size: calc(11px + 7 * ((100vw - 768px)/431));
        padding: calc(0px + 5 * ((100vw - 768px)/431)) calc(10px + 20 * ((100vw - 768px)/431));
    }
}

